import list from "@/components/list/index.vue"
export default {
	data() {
		return {
			banner:{},
			catelist:{},
			recommend:{},
			djRadios:{},
			djtoplist:{},
			djhot:{}
		};
	},
	components:{
		'list':list
	},
	onLoad() {
		this.getbanner()
		this.getcatelist()
		this.getrecommend()
		this.getdjtoplist()
		this.getdjhot()
	},
	methods:{
		getbanner(){
			this.$request({
				url:'/dj/banner'
			}).then((data) => {
				if(data.data.code == 200){
					this.banner = data.data.data
					console.log(data.data.data,'电台轮播')
				}
			})
		},
		getcatelist(){
			this.$request({
				url:'/dj/catelist'
			}).then((data) => {
				if(data.data.code == 200){
					let catelist = data.data.categories
					let i = 0
					let cata = []
					let catas = []
					let length = catelist.length-1
					catelist = catelist.map((item,index) => {
						if(i < 4 && index != length){
							cata.push(item)
							i++
						}else{
							catas = cata
							i = 0
							cata = []
							return catas
						}
					})
					catelist = catelist.filter((item)=>{
						return item
					})
					this.catelist = catelist
					console.log(data.data.categories,catelist,'电台分类')
				}
			})
		},
		getrecommend(){
			this.$request({
				url:'/dj/recommend'
			}).then((data) => {
				if(data.data.code == 200){
					let recommend = data.data
					let i = 0
					let djRadios = recommend.djRadios.map((item)=>{
						if(i < 3){
							i++
							return {
								...item,
								coverImgUrl : item.picUrl
							}
						}
					})
					djRadios = djRadios.filter(item => item)
					this.djRadios = djRadios
					this.recommend = recommend
					console.log(data.data,'电台推荐')
				}
			})
		},
		getdjtoplist(){
			this.$request({
				url:'/dj/toplist?type=new&limit=3'
			}).then((data) => {
				if(data.data.code == 200){
					let djtoplist = data.data.toplist
					djtoplist = djtoplist.map((item)=>{
						return {
							...item,
							coverImgUrl:item.picUrl,
							copywriter:item.name 
						}
					})
					this.djtoplist = djtoplist
					console.log(data.data.toplist,'热门电台榜')
				}
			})
		},
		getdjhot(){
			this.$request({
				url:'/dj/hot?limit=7'
			}).then((data) => {
				if(data.data.code == 200){
					let djhot = data.data.djRadios
					djhot = djhot.map((item)=>{
						return{
							...item,
							coverImgUrl:item.picUrl
						}
					})
					this.djhot = djhot
					console.log(data.data.djRadios,'热门电台')
				}
			})
		},
		navigateTo(id){
			uni.navigateTo({
				url: `../djtype/djtype?id=`+id
			})
		},
		getlist(id){
			// uni.navigateTo({
			// 	url: `../djdetails/djdetails?id=`+id
			// })
			if(this.$login.LOGIN()){
				uni.navigateTo({
					url: `../djdetails/djdetails?id=`+id
				})
			}else{
				// uni.showToast({
				//     title: '请先登录',
				//     duration: 2000
				// })
				uni.showModal({
				    title: '游客状态',
				    content: '请先登录',
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
							uni.switchTab({
								url: `../me/me`
							})
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		}
	}
}
